import React from 'react';

import './FileDropper.css';

export default class FileDroppper extends React.Component {
  handleDrop(event) {
    event.preventDefault();
    this.props.onFilesDrop(event.dataTransfer.files);
  }

  handleDragOver(event) {
    event.preventDefault();
  }

  render() {
    return (
      <div
        className="filedropper"
        onDrop={event => this.handleDrop(event)}
        onDragOver={event => this.handleDragOver(event)}
      >
        <div className="filedropper__text">
          {this.props.children}
        </div>
      </div>
    );
  }
}
