import distance from "@turf/distance";
import { point } from "@turf/helpers";

const FIVE_K = toKms(3.1);
const TEN_K = toKms(6.2);
const HALF_MARATHON = toKms(13.1);
const MARATHON = toKms(26.2);

function toKms (miles) {
  return miles * 1.60934;
}

export default function (points) {
  const startTime = Date.parse(points[0].time);

  points = points.map(({ lon, lat, ele, time }) => {
    return {
      point: point([lon, lat]),
      time: Date.parse(time) - startTime,
      elevation: ele,
    }
  });

  let previous = points[0];
  let elapsedDistance = 0;
  // let prevDistance = 0;

  let fiveK = FIVE_K;
  let prevFiveK = 0;
  const fiveKs = [];

  let tenK = TEN_K;
  let prevTenK = 0;
  const tenKs = [];

  let halfMarathon = HALF_MARATHON;
  let prevHalfMarathon = 0;
  const halfMarathons = [];

  let marathon = MARATHON;
  let prevMarathon = 0;
  const marathons = [];

  for (const current of points) {
    const elapsedMillis = current.time;

    elapsedDistance += distance(previous.point, current.point, {
      units: "kilometers",
    });

    if (elapsedDistance >= fiveK) {
      let frac = elapsedDistance / fiveK;
      let diff = current.time - previous.time;
      let millis = (previous.time + diff * frac) - prevFiveK;

      fiveKs.push(millis);
      fiveK += FIVE_K;
      prevFiveK = elapsedMillis;
    }

    if (elapsedDistance >= tenK) {
      let frac = elapsedDistance / tenK;
      let diff = current.time - previous.time;
      let millis = (previous.time + diff * frac) - prevTenK;

      tenKs.push(millis);
      tenK += TEN_K;
      prevTenK = elapsedMillis;
    }

    if (elapsedDistance >= halfMarathon) {
      let frac = elapsedDistance / halfMarathon;
      let diff = current.time - previous.time;
      let millis = (previous.time + diff * frac) - prevHalfMarathon;

      halfMarathons.push(millis);
      halfMarathon += HALF_MARATHON;
      prevHalfMarathon = elapsedMillis;
    }

    if (elapsedDistance >= marathon) {
      let frac = elapsedDistance / marathon;
      let diff = current.time - previous.time;
      let millis = (previous.time + diff * frac) - prevMarathon;

      marathons.push(millis);
      marathon += MARATHON;
      prevMarathon = elapsedMillis;
    }

    previous = current;
  }

  return {
    fiveKs,
    tenKs,
    halfMarathons,
    marathons,
  };
}
